<template>
  <div>
    <div :id="id" :style="{width: width, height: height}"></div>
  </div>
</template>

<script>
import echarts from "echarts";
import westeros from "./theme/westeros";
export default {
  name: "lineEcharts",
  props: {
    id: {
      type: String,
      default: "myChart"
    },
    width: {
      type: String,
      default: "100%"
    },
    height: {
      type: String,
      default: "100%"
    },
    visitorVolume: {
      type: Object,
      default: { address: [], number: [] }
    }
  },
  data() {
    return {
      chart: null 
    };
  },
  mounted() {
    this.initChart();
  },
  methods: {
    initChart() {
      this.chart = echarts.init(document.getElementById(this.id), "westeros");

      this.chart.setOption({
        title: {
          text: "点击量"
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: ["点击量"]
        },
        grid: {
          left: "1%",
          right: "3%",
          bottom: "4%",
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: this.visitorVolume.address
          // ["京" ," 津","沪" ,"渝" ,"晋" ,"辽", "吉", "苏","浙","皖", "闽" ,"赣" , "鲁", "豫" ,"鄂", "湘" , "粤", "琼" , "青", "台" ,"蜀","贵","云","陕","甘","冀" ,  "黑" , "藏"  , "港" ,"澳","宁","桂","古","新"]
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "点击量",
            type: "line",
            stack: "总量",
            data: this.visitorVolume.number
            // [8200, 6320, 5010, 4340, 3400, 2300, 1100,8200, 6320, 5010, 4340, 3400, 2300, 1100,8200, 6320, 5010, 4340, 3400, 2300, 1100,8200, 6320, 5010, 4340, 3400, 2300, 1100,8200, 6320, 5010, 4340, 3400, 2300]
          }
        ]
      });
    }
  }
};
</script>

<style scoped>
</style>



