<template>
  <div>
    <p class="title">
      <i class="el-icon-tickets"></i>最新30个访客情况
    </p>
    <el-table border :data="visitorList" style="width: 100%">
      <el-table-column type="index"></el-table-column>
      <!-- <el-table-column prop="ip" label="IP"></el-table-column> -->
      <el-table-column prop="address" label="地点"></el-table-column>

      <el-table-column prop="time" label="访问时间" sortable></el-table-column>

      <el-table-column prop="clicks" label="点击量" sortable></el-table-column>
      <el-table-column prop="eevice" label="设备" width="180"></el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "maintable",
  data() {
    return {
      visitorList: []
    };
  },
  methods: {
    handleEdit(index, row) {
      console.log(index, row);
      this.$message({
        showClose: true,
        message: index,
        row,
        type: "success"
      });
    },
    handleDelete(index, row) {
      console.log(index, row);
      this.$message({
        showClose: true,
        message: index,
        row,
        type: "success"
      });
    },
    formatter(row, column) {
      return row.address;
    },
    filterTag(value, row) {
      return row.tag === value;
    },
    filterHandler(value, row, column) {
      const property = column["property"];
      return row[property] === value;
    }
  },
  created() {
    this.$axios({
      // method: "post",
      // url: "http://localhost:8888/api/login",
      method: "get",
      url: this.$baseURL + "/visitor/findPage",
      params: { pageNum: 1, pageSize: 30 },
      headers: {
        "Content-Type": "application/json;charset=UTF-8",
        token: this.$store.getters.token
      }
    })
      .then(response => {
        this.visitorList = response.data.content;
      })
      .catch(error => {
        console.log(error); //请求失败返回的数据
      });
  }
};
</script>

<style scoped>
</style>



