import en from '../i18n/lang/en'
import Vue from 'vue'
import Router from 'vue-router'
import CommerViews from '@/views/commerViews'
import Login from '@/views/login/index'
import Layout from '@/views/layout/layout'
import HomeMain from '@/views/index/mainIndex'

// 不是必须加载的组件使用懒加载
const  ProductList = () => import('@/views/product/productList')

const  ProductCategory = () => import('@/views/product/productCategory')

const  PublishProduct = () => import('@/views/product/addProduct')

const  EditingProduct = () => import('@/views/product/editingProduct')

const  ExampleList = () => import('@/views/example/exampleList')

const  ExampleCategory = () => import('@/views/example/exampleCategory')

const  PublishExample = () => import('@/views/example/addExample')
const  EditingExample = () => import('@/views/example/editingExample')

const  SeoSetup = () => import('@/views/seo/seoSetup')
const  SeoBasics = () => import('@/views/seo/seoBasics')
const  SubmitLinks = () => import('@/views/seo/SubmitLinks')

const  addAbout = () => import('@/views/about/addArticle')
const  aboutList = () => import('@/views/about/list')

const  EditingAbout = () => import('@/views/about/editingAbout')
const  RotationChart = () => import('@/views/advertisement/rotationChart')
const  Other = () => import('@/views/advertisement/other')

const Message = () => import('@/views/message/message')
const Notice = () => import('@/views/content/notice')
const Advantage = () => import('@/views/content/advantage')
const FriendshipLink = () => import('@/views/content/link')

const NotFound = () => import('@/page404')
const AddArticle = () => import('@/views/article/addArticle')
const ArticleCategory= () => import('@/views/article/ArticleCategory')
const ArticleList = () => import('@/views/article/ArticleList')
const EditingArticle = () => import('@/views/article/editingArticle')
const Rewrite = () => import('@/views/systemTools/rewrite')
const AdminList = () => import('@/views/syssetting/adminList')
const Company = () => import('@/views/syssetting/company')
const ThemeList = () => import('@/views/theme/ThemeList')


Vue.use(Router)
let routeNmae = en.routeNmae
let defaultRouter = [
  { path: '/',
    redirect: '/index',
    hidden: true,
    children: []
  },
  {
    path: '/login',
    component: Login,
    name: '',
    hidden: true,
    children: []
  },
  {
    path: '/index',
    iconCls: 'fa fa-dashboard', // 图标样式class
    name: routeNmae.home,
    component: Layout,
    alone: true,
    children: [
      {
        path: '/index',
        iconCls: 'fa fa-dashboard', // 图标样式class
        name: '主页',
        component: HomeMain,
        children: []
      }
    ]
  },
  {
    path: '/404',
    component: NotFound,
    name: '404',
    hidden: true,
    children: []
  },
  {
    path: '/index',
    iconCls: 'fa fa-dashboard', // 图标样式class
    name: routeNmae.home,
    component: Layout,
    hidden: true,
    children: [
      {
        path: '/editingArticle',
        iconCls: 'fa fa-dashboard', // 图标样式class
        name: routeNmae.editingArticle,
        component: EditingArticle,
        children: []
      },
      {
        path: '/editingProduct',
        iconCls: 'fa fa-dashboard', // 图标样式class
        name: routeNmae.editingProduct,
        component: EditingProduct,
        children: []
      },
      {
        path: '/editingExample',
        iconCls: 'fa fa-dashboard', // 图标样式class
        name: routeNmae.editingExample,
        component: EditingExample,
        children: []
      },
      {
        path: '/EditingAbout',
        iconCls: 'fa fa-dashboard', // 图标样式class
        name: routeNmae.editingAbout,
        component: EditingAbout,
        children: []
      }
    ]
  },
]

let addRouter = [
  {
    path: '/',
    iconCls: 'el-icon-sort', // 图标样式class
    name: routeNmae.interaction,
    component: Layout,
    meta: {role:  ['superAdmin', 'admin','CSD']},
    children: [
      {
        path: '/message',
        iconCls: 'el-icon-upload2', // 图标样式class
        name: routeNmae.message,
        component: Message,
        children: []
      }
    ]
  }
  
  ,
  {
    path: '/',
    iconCls: 'el-icon-s-marketing', // 图标样式class
    name: routeNmae.advertisement,
    meta: {role: ['superAdmin', 'admin']},
    component: Layout,
    children: [
      {
        path: '/rotationChart',
        iconCls: 'el-icon-picture-outline', // 图标样式class
        name: routeNmae.rotationChart,
        component: RotationChart,
        children: []
      },{
        path: '/other',
        iconCls: 'el-icon-picture-outline-round', // 图标样式class
        name: routeNmae.other,
        component: Other,
        children: []
      }
    ]
  }
  ,
  {
    path: '/',
    iconCls: 'el-icon-warning-outline', // 图标样式class
    name: routeNmae.about,
    meta: {role: ['superAdmin', 'admin']},
    component: Layout,
    children: [
      {
        path: '/addAbout',
        iconCls: 'el-icon-plus', // 图标样式class
        name: routeNmae.publishAbout,
        component: addAbout,
        children: []
      },
      {
        path: '/aboutList',
        iconCls: 'el-icon-files', // 图标样式class
        name: routeNmae.aboutList,
        component: aboutList,
        children: []
      }
    ]
  }
  ,
  {
    path: '/',
    iconCls: 'el-icon-tickets', // 图标样式class
    name: routeNmae.article,
    meta: {role: ['superAdmin', 'admin']},
    component: Layout,
    children: [
      {
        path: '/addArticle',
        iconCls: 'el-icon-plus', // 图标样式class
        name: routeNmae.publishArticle,
        component: AddArticle,
        children: []
      },
      {
        path: '/articleCategory',
        iconCls: 'el-icon-menu', // 图标样式class
        name: routeNmae.articleCategory,
        component: ArticleCategory,
        children: []
      },
      {
        path: '/articleList',
        iconCls: 'el-icon-files', // 图标样式class
        name: routeNmae.articleList,
        component: ArticleList,
        children: []
      }
    ]
  },
  {
    path: '/', 
    iconCls: 'el-icon-s-shop', // 图标样式class
    name: routeNmae.product,
    meta: {role: ['superAdmin', 'admin']},
    component: Layout,
    children: [
      {
        path: '/addProduct',
        iconCls: 'el-icon-plus', // 图标样式class
        name: routeNmae.publishProduct,
        component: PublishProduct,
        children: []
      }, {
        path: '/productCategory',
        iconCls: 'el-icon-menu', // 图标样式class
        name: routeNmae.productCategory,
        component: ProductCategory,
        children: []
      }, {
        path: '/productList',
        iconCls: 'el-icon-files', // 图标样式class
        name: routeNmae.productList,
        component: ProductList,
        children: []
      },
    ]
  },
  {
    path: '/',
    iconCls: 'el-icon-thumb', // 图标样式class
    name: routeNmae.example,
    meta: {role: ['superAdmin', 'admin']},
    component: Layout,
    children: [
      {
        path: '/publishExample',
        iconCls: 'el-icon-plus', // 图标样式class
        name: routeNmae.publishExample,
        component: PublishExample,
        children: []
      }, {
        path: '/exampleCategory',
        iconCls: 'el-icon-menu', // 图标样式class
        name: routeNmae.exampleCategory,
        component: ExampleCategory,
        children: []
      }, {
        path: '/exampleList',
        iconCls: 'el-icon-files', // 图标样式class
        name: routeNmae.exampleList,
        component: ExampleList,
        children: []
      },
    ]
  },
  {
    path: '/',
    iconCls: 'el-icon-eleme', // 图标样式class
    name: routeNmae.SEO,
    component: Layout,
    meta: {role: ['superAdmin', 'admin']},

    children: [
      {
        path: '/seoSetup',
        iconCls: 'el-icon-setting', // 图标样式class
        name: routeNmae.seoSetup,
        component: SeoSetup,
        children: []
      },{
        path: '/submitLinks',
        iconCls: 'fa fa-sort-amount-asc', // 图标样式class
        name: routeNmae.submitLinks,
        component: SubmitLinks,
        children: []
      },
      {
        path: '/seoBasics',
        iconCls: 'fa fa-sliders', // 图标样式class
        name: routeNmae.SeoBasics,
        component: SeoBasics,
        children: []
      },
      
    ]
  },

  // {
  //   path: '/',
  //   iconCls: 'el-icon-user',
  //   name: routeNmae.user,
  //   component: Layout,
  //   meta: {role: ['superAdmin']},
  //   children: [
  //     {
  //       path: '/erji1',
  //       iconCls: 'fa fa-server',
  //       name: routeNmae.userList,
  //       component: Erji,
  //       children: []
  //     }
  //   ]
  // },

  {
    path: '/',
    iconCls: 'el-icon-edit', // 图标样式class
    name: routeNmae.content,
    component: Layout,
    meta: {role: ['superAdmin', 'admin']},
    children: [
      {
        path: '/notice',
        iconCls: 'fa fa-file-code-o', // 图标样式class
        name: routeNmae.notice,
        component: Notice,
        children: []
      }
      ,
      {
        path: '/advantage',
        iconCls: 'fa fa-file-code-o', // 图标样式class
        name: routeNmae.advantage,
        component: Advantage,
        children: []
      }
      ,
      {
        path: '/friendshipLink',
        iconCls: 'fa fa-file-code-o', // 图标样式class
        name: routeNmae.friendshipLink,
        component: FriendshipLink,
        children: []
      }
    ]
  },
  {
    path: '/',
    iconCls: 'el-icon-scissors', // 图标样式class
    name: routeNmae.systemTools,
    component: Layout,
    meta: {role: ['superAdmin','admin']},
    children: [
      {
        path: '/rewrite',
        iconCls: 'el-icon-loading', // 图标样式class
        name: routeNmae.rewrite,
        component: Rewrite,
        children: []
      }
    ]
  },
  { path: '*',
    redirect: '/404',
    hidden: true,
    children: []
  },
  {
    path: '/',
    iconCls: 'fa fa-universal-access', // 图标样式class
    name: routeNmae.theme,
    component: Layout,
    meta: {role: ['superAdmin','admin']},

    children: [
      {
        path: '/themeList',
        iconCls: 'fa fa-expeditedssl', // 图标样式class
        name: routeNmae.themeList,
        component: ThemeList,
        children: []
      }
    ]
  },
  
  {
    path: '/',
    iconCls: 'el-icon-setting', // 图标样式class
    name: routeNmae.systemSettings,
    component: Layout,
    meta: {role: ['superAdmin']},
    children: [
      {
        path: '/basicSetup',
        iconCls: 'el-icon-s-home', // 图标样式class
        name: routeNmae.basicSetup,
        component: Company,
        children: []
      },
      {
        path: '/adminList',
        iconCls: 'el-icon-user', // 图标样式class
        name: routeNmae.adminList,
        component: AdminList,
        children: []
      }
    ]
  }

]


export default new Router({
  routes: defaultRouter
})
export {defaultRouter, addRouter}



