<template>
  <div>
    <el-row>
      <el-col :span="24">
        <!-- <div class="screen">
          <el-radio v-model="timeSlot" @change="getVisitor()" label="today">今天</el-radio>
          <el-radio v-model="timeSlot" @change="getVisitor()" label="yesterday">昨天</el-radio>
          <el-radio v-model="timeSlot" @change="getVisitor()" label="fiveDays">最近5天</el-radio>
          <el-radio v-model="timeSlot" @change="getVisitor()" label="sevenDays">最近7天</el-radio>
          <el-radio v-model="timeSlot" @change="getVisitor()" label="thirtyDays">最近30天</el-radio>
          <el-radio v-model="timeSlot" @change="getVisitor()" label="total">总访客</el-radio>
        </div> -->
        <line-echarts
          :visitorVolume="visitorVolume"
          id="lineEcharts"
          height="300px"
          ref="echarts"
          v-if="flag"
        ></line-echarts>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <maintable id="maintable"></maintable>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import LineEcharts from "../../components/ECharts/lineEcharts";
import Maintable from "../table/maintable";

export default {
  name: "mainIndex",
  components: { Maintable, LineEcharts },
  data() {
    return {
      flag: false,
      timeSlot: "today",
      visitorVolume: {}
    };
  },
  created() {},
  mounted() {
    this.selfAdaption();
    this.findVisitorVolume();
  },
  methods: {
    findVisitorVolume() {
      this.$axios({
        // method: "post",
        // url: "http://localhost:8888/api/login",
        method: "get",
        url: this.$baseURL + "/visitor/findVisitorVolume",

        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          this.visitorVolume = response.data.visitorVolume;
          this.flag = true;
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
        });
    },
    // echart自适应
    selfAdaption() {
      let that = this;
      setTimeout(() => {
        window.onresize = function() {
          if (that.$refs.echarts) {
            that.$refs.echarts.chart.resize();
          }
        };
      }, 10);
    },
    getVisitor() {
      console.log(this.timeSlot);
    }
  }
};
</script>

<style lang="scss">
$top: top;
$bottom: bottom;
$left: left;
$right: right;
$leftright: ($left, $right);
$pinkk: #eec2d3;
$bluee: #409eff;
$yelloww: #f4d884;
$grennn: #89dda0;
$purplee: #78a2ea;
$lightBluee: #b8d6ff;

$list: bluee pinkk yelloww grennn purplee lightBluee;
$list1: $bluee $pinkk $yelloww $grennn $purplee $lightBluee;
%shadow {
  background: #fff;
  -webkit-box-shadow: 4px 4px 40px rgba(0, 0, 0, 0.2);
  box-shadow: 4px 4px 40px rgba(0, 0, 0, 0.2);
  border-color: rgba(0, 0, 0, 0.2);
  .title {
    font-size: 14px;
    padding: 10px;
    i {
      margin-#{$right}: 5px;
    }
  }
}

@mixin flex($direction: row, $content: space-between) {
  display: flex;
  flex-direction: $direction;
  justify-content: $content;
}
.card {
  color: #666;
  @extend %shadow;

  ul {
    @include flex;
    li {
      flex: 1;
      a {
        color: #666666;
        align-items: center;
        padding-#{$top}: 20px;
        padding-#{$bottom}: 20px;
        @include flex(column);
        span {
          height: 44px;
        }
        .num {
          line-height: 44px;
          font-size: 42px;
          color: $bluee;
          margin: 0px;
        }
      }
      .kjfs-bluee {
        color: $bluee;
      }
      .kjfs-pinkk {
        color: $pinkk;
      }
      .kjfs-yelloww {
        color: $yelloww;
      }
      .kjfs-grennn {
        color: $grennn;
      }
      .kjfs-purplee {
        color: $purplee;
      }
      .kjfs-lightBluee {
        color: $lightBluee;
      }
      &:hover {
        .kjfs-bluee {
          color: #ffffff;
          background: $bluee;
        }
        .kjfs-pinkk {
          color: #ffffff;
          background: $pinkk;
        }
        .kjfs-yelloww {
          color: #ffffff;
          background: $yelloww;
        }
        .kjfs-grennn {
          color: #ffffff;
          background: $grennn;
        }
        .kjfs-purplee {
          color: #ffffff;
          background: $purplee;
        }
        .kjfs-lightBluee {
          color: #ffffff;
          background: $lightBluee;
        }
      }
    }
  }
  .table {
    padding: 21px;
    p {
      height: 52px;
      line-height: 52px;
      border: 1px solid #cccccc;
      overflow: hidden;
      border-#{$top}: none;
      @include flex(null, start);
      &:first-child {
        border-#{$top}: 1px solid #cccccc;
      }
      span {
      }
      .tit {
        width: 90px;
        min-width: 90px;
        height: 100%;
        text-align: center;
        border-#{$right}: 1px solid #cccccc;
        margin-#{$right}: 18px;
      }
      span.gitbox {
        flex: 1;
        height: 100%;
        overflow: hidden;
        @include flex(row, start);
        a {
          &:first-child {
            margin-#{$right}: 30px;
          }
        }
      }
    }
  }
}
#lineEcharts {
  // margin-#{$top}: 30px;
  padding-#{$top}: 30px;
  @extend %shadow;
}
#maintable {
  margin-#{$top}: 30px;
  padding-#{$top}: 10px;
  @extend %shadow;
}
.screen {
  background: #fff;
  text-align: center;
  padding: 14px;
}
</style>



